.nav {
  &__link {
    position: relative;
    color: inherit;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    color: $black-color;

  &::after {
    content: "";
    position: absolute;
    bottom: -8px;
    height: 1px;
    display: block;
    width: 100%;
    background-color: $black-color;
    transition: transform 0.3s;
    transform-origin: left;
    transform: scale(0);
  }

  &:hover::after {
    transform: scale(1);
  }
}
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}
